import React from 'react';
import { Row, Col, Button, Typography } from 'antd';
import { facebookProvider, googleProvider } from '../../firebase/authMethods';
import socialMediaAuth from '../../service/auth';

const { Title } = Typography;

export default function Login() {
    const handleOnclick = async (provider) =>{
        const res = await socialMediaAuth(provider);
        console.log(res);
    }
    return (
        <div>
            <Row justify='center' style={{height: 800}}>
                <Col span={8}>
                    <Title style={{textAlign: 'center'}} level={3}> Fun Chat</Title>
                    <Button style={{width: '100%', marginBottom: 5}} onClick={() => handleOnclick(googleProvider)}>
                        Đăng nhập bằng Google
                    </Button>
                    <Button style={{width: '100%'}} onClick={() => handleOnclick(facebookProvider)}>
                        Đăng nhập bằng Facebook
                    </Button>
                </Col>
            </Row>
        </div>
    );
}